body {
  background-image: linear-gradient(to right, rgba(46, 47, 66, .2), rgba(46, 47, 66, .2)), url("images.a5852093.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  overflow: hidden;
}

.container {
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 43%;
  right: 50%;
  transform: translate(50%, -50%);
}

#datetime-picker {
  text-align: center;
  background-color: #cfeeee;
  padding: 20px;
  font-size: 25px;
}

.date-inpute {
  margin-right: 25px;
  position: relative;
  overflow: hidden;
}

.overlay {
  width: 100%;
  height: 100%;
  opacity: .3;
  pointer-events: auto;
  pointer-events: none;
  background-color: green;
  padding-top: 40px;
  padding-left: 32px;
  padding-right: 32px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.date-inpute:hover .overlay {
  transform: translateX(0%);
}

.date-inpute:hover #datetime-picker {
  border-color: red;
  outline: none;
}

[data-start] {
  width: 100px;
  height: 35px;
  background-color: #f3bdbd;
  font-size: 25px;
}

.button-is-active {
  background-color: #7fff00;
}

.timer {
  justify-content: center;
  gap: 30px;
  padding-top: 20px;
  display: flex;
  position: fixed;
  top: 10%;
  right: 50%;
  transform: translateX(50%);
}

.field {
  flex-direction: column;
  display: flex;
}

.value {
  width: 125px;
  text-align: center;
  border: 3px solid #080808;
  padding: 20px;
  font-size: 35px;
  font-weight: 700;
}

.label {
  text-transform: uppercase;
  text-align: center;
  font-size: 30px;
}

